    chattingCode:
`public synchronized void broadcast(String message) {
    Enumeration<ChatHandler> enu = userVec.elements();
    while(enu.hasMoreElements()) {
        ChatHandler handler = enu.nextElement();
        try {
            handler.dataOut.writeUTF(message);
            handler.dataOut.flush();
        } catch (IOException e) {
            // TODO Auto-generated catch block
            e.printStackTrace();
        }
    }
}`,

    chattingCode2:
`@Override
public void run() {
    while (!Thread.interrupted()) {
        try {
            String message = dataIn.readUTF();
            String name = message.split(" : ")[0];
            String msg = message.split(" : ")[1];
            if (msg.equals("@") || msg.equals("@")) {
                chatFunction(msg, name);
                continue;
            }
            if (name.equals(userName)) {
                chatPage.addMyMessage(message);
            } else {
                if (pageInfo.getPageNum() != 5 && pageInfo.getAlarm() == 1) {
                    Object[] options = {" ", ""};
                    int result = JOptionPane.showOptionDialog(chatPage, message, " ", JOptionPane.YES_NO_OPTION, JOptionPane.QUESTION_MESSAGE, null, options, options[0]);
                    if(result == JOptionPane.YES_OPTION) {
                        moneyPage.changeChatPage();
                    }
                }
                chatPage.addOtherMessage(message);
            }
            // taOutput.append(message);
        } catch (IOException e) {
            // taOutput.append("----------");
            if (th != null) {
                th.interrupt();
                th = null;
            }
            // e.printStackTrace();
        }
    }
}`,

