import {NoteImgSet5} from '../SliderSection/NoteImages5'

export const noteHighlight5 = {
    slides: NoteImgSet5,

    profileCode:
`$('#button1').click( function() {
    if( $(this).val() == '' ) {
      $(this).val('');
      $('#pictureview').css({
            'height': '680px'
        })
        $('#profile').css({
            'height' : '1135px'
        })
        $( 'html, body' ).stop().animate( { scrollTop : '0' } )
    }
    else {
      $(this).val('');
      $('#pictureview').css({
            'height': 'auto',
           'overflow': 'hidden'
        })

        $('#profile').css({
            'height' : 'auto'
        })
    }
});`,

    profileDesc:
    `       , ,    
      ,        .    
    6  ''     JavaScript    
    div height auto       .`,

    profileDesc2:
    `          
      .     , , , 
      .`,
}