import {MoneyImgSet6} from '../SliderSection/MoneyImages6'

export const moneyHighlight6 = {
    slides: MoneyImgSet6,

    chartCode:
`void drawChartIncome() { //  

    int sum = 0; //  0
    for (int i = 0; i < recordPage.data.length; i++) { //   
        if (recordPage.data[i][2].equals("")) {
            sum += Integer.parseInt(recordPage.data[i][4]);
        }
    }
    if (sum == 0)
        return;

    int[] categorySum = new int[6];
    String[] categoryName = {"", "", "", "", "", ""};
    this.categoryName = categoryName;
    arcAngle = new int[categoryName.length];

    for (int i = 0; i < recordPage.data.length; i++) {
        if (recordPage.data[i][2].equals("")) {
            switch (recordPage.data[i][3]) {
            case "": categorySum[0] += Integer.parseInt(recordPage.data[i][4]); break;
            case "": categorySum[1] += Integer.parseInt(recordPage.data[i][4]); break;
            case "": categorySum[2] += Integer.parseInt(recordPage.data[i][4]); break;
            case "": categorySum[3] += Integer.parseInt(recordPage.data[i][4]); break;
            case "": categorySum[4] += Integer.parseInt(recordPage.data[i][4]); break;
            case "": categorySum[5] += Integer.parseInt(recordPage.data[i][4]); break;

            default: break;
            }
        }
    }

    for (int i = 0; i < categorySum.length; i++) {
        arcAngle[i] = (int) Math.round(categorySum[i] / (double) sum * 360);
        chartPanel.repaint(); //  PAINT
    }
}`,

    chartCode2:
`class ChartPanel extends JPanel { //   

    public void paintComponent(Graphics g) {

        super.paintComponent(g);//  

        int startAngle = 0;
        int gap = 0;
        if(categoryName != null) {
            for (int i = 0; i < categoryName.length; i++) {
                gap += 30;
                g.setFont(new Font("JetBrains Mono", Font.PLAIN, 18));
                g.setColor(color[i]);
                g.drawString(categoryName[i] + " " + Math.round(arcAngle[i] * 100 / 360) + "%", 50, 30 + gap);
            }
            for (int i = 0; i < categoryName.length; i++) {
                g.setColor(color[i]);
                g.fillArc(230, 70, 250, 250, startAngle, arcAngle[i]);
                startAngle = startAngle + arcAngle[i];
            }
        }

    }
}`,

    chartDesc:
    `   ''             . 
    / toggle          .
     drawChartIncome           
       categorySum    .   
         for  categorySum        
      arcAngle   chartPanel  repaint()    .`,

    chartDesc2:
    `drawChartIncome      arcAngle     
     .`,

}