import {NoteImgSet2} from '../SliderSection/NoteImages2'

export const noteHighlight2 = {
    slides: NoteImgSet2,

    signupCode:
`$('#filepath').on('change', function(){
		var str = $('#filepath').val();

		//   
		var patt = /(.jpeg$|.jpg$|.gif$|.png$)/gi;
		var result = str.match(patt);

		if($("#filepath").val() === ""){
			alert(" .")
			$('#profilepic').attr("src", "/usersettingview/images/person.png");
			return false;
		}

		if(!result){
			alert('jpeg, jpg, gif, png .');
			return false;
		}

		//   
		if(this.files[0].size > 100000000){
			alert('100MB  .');
			$('#filepath').val("");
			return false;
		}

		//    FileReader 
		var reader = new FileReader();

		// File  dataURL  
		reader.readAsDataURL(this.files[0]); //    [i]   .

		//       
		reader.onload = function(e){
			// img src  File .
			$('#profilepic').attr('src', e.target.result);
		};
	}); // end change()`,

    signupDesc:
    `      .     
    JavaScript   .      
     (e.g. jpeg, png, etc.)        .
           ( 1 ). 
          <img /> src   .`,

}