import {MoneyImgSet1} from '../SliderSection/MoneyImages'

export const moneyHighlight = {
    slides: MoneyImgSet1,

    loginCode:
`public int loginCheck(String id, String pw) {
    String pwCheck = "";
    int chk = -1;
    try {
        conn = Jdbc.getInit();
        String sql = "select user_pw from bank_user_info where user_id = ?";
        pstmt = conn.prepareStatement(sql);
        pstmt.setString(1, id);
        rs = pstmt.executeQuery();
        if (rs.next()) {
            pwCheck = rs.getString("user_pw");
            if (pwCheck.equals(pw)) {
                chk = 1;
            } else {
                chk = 0;
            }
        } else {
            chk = -1;
        }
    } catch (ClassNotFoundException | SQLException e) {
        e.printStackTrace();
    } finally {
        Jdbc.close(rs);
        Jdbc.close(pstmt);
        Jdbc.close(conn);
    }
    return chk;
}`,
    loginDesc:
    `    . ID   
       .     chk  -1 
     .       chk  0,   1 
      chk  1      ,     UserInfo      CRUD 
           .`,

    loginDesc2:
    `        '' 
          ,   
          .`,

    loginDesc3:
    `       (    )    
         .`,

    loginDesc4:
    `Java Oracle DBMS   moneymoney  . Java  JFrame  
    GUI  Calendar Thread   , Socket ServerSocket    
    . ,        Graphics  paintComponent()  .
      Oracle    DAO     , DTO      
     .`,
}